<template>
    <div class="character-sheet">
            <stat-display></stat-display>

        

        <div class="buttons-wrap-list">
            <div class="btn" @click="inventoryOpen=true">INVENTORY</div>
            <div class="btn" @click="closeChar">CLOSE</div>
        </div>
        <div class="buttons-wrap-list">
            <div class="btn" @click="specialClassMenu=true">{{specialAction}}</div>
            <div class="btn" >Prepare spells</div>
        </div>
        <!-- inventory section -->
        <inventory v-if="inventoryOpen" @closeInventory="inventoryOpen=false"></inventory>
        
    </div>
    
    
</template>

<script>
import statDisplay from '../components/statDisplayComponent.vue';

import inventory from './inventory.vue'
export default {
    components: {
        inventory,
        statDisplay,
    },
    data() {
        return {
            inventoryOpen: false,
            specialClassMenu: false,
            
        }
    },
    methods: {
        closeChar() {
            this.$emit('closeChar')
        }
    },
    computed: {
    playerCharacter() {
      return this.$store.state.playerCharacter;
    },
    charHPcomp() {
      return this.$store.getters.characterHp;
    },
    charAPcomp() {
      return this.$store.getters.actionPoints;
    },
    specialAction() {
        if (this.$store.state.playerCharacter.characterClass ==='hunter') {
            return 'Prepare traps'
        } else if (this.$store.state.playerCharacter.characterClass === 'engineer') {
            return 'Craft armour'
        } else {
            return 'crafting'
        }
    }
  },
  created() {

  }
}
</script>

<style scoped>
.character-sheet {
    height: 100vh;
    width: 100%;
    display: grid;
    grid-gap: 1rem;
    align-content: center;
    position: absolute;
    background: #000408;

}


</style>
