<template>
  <div class="character-creation-module">
    <stat-display></stat-display>
    <div class="creation-list-wrap">
      <ul class="creation-list">
        <li @click="nameOpen=true">NAME</li>
        <li @click="classOpen=true">CLASS</li>
        <li @click="statsOpen=true">STATISTICS</li>
        <li @click="openAbilitiesFn">ABILITIES</li>
        <li @click="backgroundOpen=true">BACKGROUND</li>
        
      </ul>
      
    </div>
    <div class="buttons-wrap-list">
        <div class="btn" @click="cancelCharacterCreation">BACK</div>
        <div class="btn" @click="acceptCharacterCreation">ACCEPT</div>
    </div>
    
    <!-- name input -->
    <div class="name-section slide-modal" v-if="nameOpen">
      <div class="modal-content name-content">
      <h1>Your name:</h1>
        
      <input class="name-input" type="text" v-model="charName" maxlength="16">
      <div class="select-portrait">
        <h1>Portrait:</h1>
        <div class="portrait-large">
                    <img src='../assets/portraits/portrait.jpg' alt="">

                </div>
                <div class="buttons-wrap-list">
                  <div class="btn">Next</div>
                <div class="btn">Previous</div>
                </div>
                
                
      </div>
      <div >
        <div class="back-button btn" @click="applyName"> DONE</div>
      </div>
      
      </div>
    </div>
    <!-- class select -->
    <div class="class-select slide-modal" v-if="classOpen">
        <div class="modal-content class-modal">
      <h1 class="modal-title">Character class</h1>
          
          <div class="class-select-section">
            <div class="select-engineer select-class" @click="selectClass('engineer', 0)" :class="{'selected':classSelected=='engineer'}">Engineer</div>
            <div class="select-hunter select-class" @click="selectClass('hunter', 1)" :class="{'selected':classSelected=='hunter'}">Hunter</div>
            <div class="select-engineer select-class" @click="selectClass('scholar', 2)" :class="{'selected':classSelected=='scholar'}">Scholar</div>
            
            
          </div>
          <div class="class-description-section">
            <p>{{classDescs[classSelected + 'desc']}}</p>
          </div>
          <div class="buttons-wrap-list">
            <div class="apply-button btn" @click="acceptClass">ACCEPT</div>
            <div class="back-button btn" @click="cancelClass">CANCEL</div>
          </div>
        
      </div>
    </div>
    <!-- background selection -->
    <background-select v-if="backgroundOpen" @backgroundClose='backgroundOpen=false' @backgroundCloseApply="applyBackground($event)"></background-select>
    <!-- abilities distribution -->
    <abilities-distribution v-if="abilitiesOpen" @closeAbilities="abilitiesOpen=false"></abilities-distribution>
    <!-- stat distribution -->
    <stat-distribution v-if="statsOpen" @closeStats="applyStatistics()"></stat-distribution>
    <!-- info modal -->
    <transition name="show-info-modal">
      <info-modal v-if="showModal" :msg="infoMsg"></info-modal>
    </transition>

      
  </div>
</template>

<script>
// @ is an alias to /src
import abilitiesDistribution from '../components/abilitiesDistribution.vue'
import infoModal from '../components/infoModal.vue'
import statDistribution from '../components/statDistribution.vue'
import backgroundSelect from '../components/backgroundSelect.vue'
import statDisplay from '../components/statDisplayComponent.vue';


export default {
  name: 'characterCreation',
  components: {
    statDisplay,
    abilitiesDistribution,
    infoModal,
    statDistribution,
    backgroundSelect,
  },
  data() {
    return {
      showModal: false,
      infoMsg: '',
      nameOpen: false,
      classOpen: false,
      statsOpen: false,
      abilitiesOpen: false,
      backgroundOpen: false,
      classSelected: '',
      charName: '',

      charBackground: null,

      classDescs: {
        desc: 'select a class',
        hunterdesc: 'a hunter class, has traps and shit',
        engineerdesc: 'an engineer class, has an armor and crafts stuff',
        scholardesc: 'class currently not available'
      },

      //checks for every category completed
      nameDone: false,
      classDone: false,
      statsDone: false,
      abilitiesDone: false,
      backgroundDone: false,
    }
  },
  methods: {
    selectClass(charClass, charNum) {
      this.classSelected = charClass;
      this.$store.state.playerCharacter.characterClass = charClass;
      this.$store.state.playerCharacter.abilitiesPreset = charNum;
      
    },
    applyName() {
      this.$store.state.playerCharacter.characterName = this.charName;
      this.nameOpen = false;
      this.nameDone = true;
    },
    applyBackground(background) {
      if (JSON.stringify(this.$store.state.playerCharacter.charBackground) === JSON.stringify(background)) {
        this.backgroundOpen = false;
        return;
      }
      this.$store.state.playerCharacter.charBackground = background;
      switch (background.name) {

        case "reasonFindCure":
        // if (this.$store.playerCharacter.charBackground === reasonOldFlame || this.$store.playerCharacter.charBackground === reasonStudyDisease) {retract any previous changes
        //that the other backgrounds made}
        //do this for every background variant
        this.$store.commit('increaseBleakness', 50);
        break;

        case "reasonOldFlame":
        //get rid of the +50 from reasonFindCure
        // get rid of study disease bonus
        if ( this.$store.state.playerCharacter.bleakness === 50) {
          this.$store.commit('increaseBleakness', -50);
        }
        break;

        case "reasonStudyDisease":
        //get rid of old flame bonus
        //get rid of the +50 from reasonFindCure        
        if ( this.$store.state.playerCharacter.bleakness === 50) {
          this.$store.commit('increaseBleakness', -50);
        }
        break;  

        default: 
        break;
      }
      this.backgroundOpen = false;
      this.backgroundDone = true;
      
    },
    cancelBackground() {
      this.$store.state.playerCharacter.charBackground = '';
      this.$store.state.playerCharacter.bleakness = 0;
      //cancel other effects
      this.backgroundDone = false;
      
    },
    acceptClass(charClass) {
      this.classOpen = false;
      this.classDone = true;
      
    },
    cancelClass() {
      this.classOpen = false;      
      this.$store.state.playerCharacter.characterClass = '';
      this.$store.state.playerCharacter.abilitiesPreset = null;
      this.classDone = false;
      
    },
    applyStatistics(){
      this.statsOpen = false;
      this.statsDone = true;
      
    },
    showModalFn(msg) {
      this.infoMsg = msg;
      this.showModal = true;
      setTimeout ( () => {
        this.showModal = false;
        }, 1000)
    },
    openAbilitiesFn() {
      
      if(this.$store.state.playerCharacter.abilitiesPreset === null) {
        this.showModalFn('Please select a character class before choosing abilities')
      } else {
        this.abilitiesOpen=true;
      }
    },
    cancelCharacterCreation() {
      this.$store.state.playerCharacter = {characterName: '',
            statistics: [{name: 'Vitality', value: 1}, {name: 'Strength', value: 1}, {name:'Dexterity', value: 1}, {name: 'Arcane', value: 1}, {name:'Luck', value: 1}],
            characterClass: '',
            abilitiesPreset: null,
            characterName: '',
            pointsToAllocate: 5,
            abilityPoints: 1,
            charBackground: {},
            bleakness: 0,
            
            //inventory
            playerEquippedItems: {helmet: {name: 'helmet', imgUrl: 'blunderbuss.svg', slot: 'helmet'}, armor: {name: 'armor', imgUrl: 'armor.svg'}},
            playerInventoryHelmets: [],
            generalAbilityModifiers: {HPmodifier: 0, initiativeModifier: 0, APmodifier: 0 },
            hunterAbilityModifiers: {snareTraps: false, trapsLevel: 0},
            engineerAbilityModifiers: {craftArmour: false, armourLevel: 0},
            

            //abilities
            abilitySets: [ {abilitySetChar: 'engineer', abilities: [{name:'ability engineer dummy1', desc: 'dummy engineer desc', effectDesc: 'effect desc'}, {name: 'ability engineer dummy2', desc: 'dummy engineer desc 2', effectDesc: 'effect desc'}]}, {abilitySetChar: 'hunter', abilities: [{name:'Snare trap', desc: 'Basic type of a trap. Simple to set yet highly effective.', effectDesc: 'Activates after 1 turn. Lowers enemy initiative by 1 for 3 turns.', status: 0}, {name: 'ability hunter dummy2', desc: 'dummy hunter desc 2', effectDesc: 'effect desc'}]}, {abilitySetChar: 'scholar', abilities: [{name:'ability scholar dummy1', desc: 'dummy scholar desc', effectDesc: 'effect desc'}, {name: 'ability scholar dummy2', desc: 'dummy scholar desc 2', effectDesc: 'effect desc'}]}],
            abilities: [{innerName: 'manofaction', name: 'Man of action', desc: 'You could never sit in one place for too long.', effectDesc: 'Increases action points by 1', target: 'APmodifier', targetValue: 1, status: 0, abilityType: 'modifier', locked: false}],
       
        
        }
        this.$store.state.gameState.isNewGame = false;
        this.$store.state.gameState.characterCreated = false;
        this.$router.push('/')
        
    },
    acceptCharacterCreation() {
      //check if every aspect of char creation is done
      if (this.nameDone == false ||
      this.classDone == false ||
      this.statsDone == false ||
      //this.abilitiesDone == false ||
      this.backgroundDone == false) {
        this.showModalFn('Finish character creation before starting the game');
      } else {
        this.$router.push('/gameSection')
      }
        // this.$router.push('/gameSection')
      
    }
  },
  computed: {
    
  }
}
</script>

