<template>
    <div class="background-select">
        
        <h1>Background</h1>
        <p>What is the reason you came to the town of Blackdwell?</p>
        <div class="reason-select-wrap">
            <div class="reason" @click="chooseReason('reasonOldFlame', 'An old flame', 'Opens an additional quest chain')" :class="{'selected-reason': reasonOldFlame}">
                <h5 class="highlight">An old flame</h5>
                <p>Someone here was important for me, once. Or maybe still is.</p>
                <p class="highlight"> Opens an additional quest chain </p>
            </div>

            <div class="reason" @click="chooseReason('reasonStudyDisease', 'Study the disease', '+1 to relations with Engineer\'s and Sholars\' guilds')" :class="{'selected-reason': reasonStudyDisease}">
                <h5 class="highlight">Study the disease</h5>
                <p>This mysterious 'Bleakness' is no illness of the likes we have ever seen. It has to be studied.</p>
                <p class="highlight">+1 to relations with Engineers' and Sholars' guilds</p>
            </div>

            <div class="reason" @click="chooseReason('reasonFindCure', 'Find the cure for the disease', '+50 Bleakness')" :class="{'selected-reason': reasonFindCure}">
                <h5 class="highlight">Search for the cure</h5>
                <p>I am afflicted by Bleakness. Someone told me that this is the birthplace of the disease. If a cure exists, it must be here. </p>
                <p class="highlight">+50 Bleakness meter</p>
            
            </div>
        </div>

        <!-- confirmation message -->
        <transition name="show-info-modal">
            <info-modal v-if="showModal" :msg="infoMsg"></info-modal>
        </transition>

        <div class="buttons-wrap-list">
            <div class="btn" @click="closeBackgroundApply">CONFIRM</div>
            <div class="btn" @click="closeBackground">CANCEL</div>
        </div>
    </div>
</template>

<script>
import infoModal from '../components/infoModal.vue'

export default {
    components: {
    infoModal,
        
    },
    data() {
        return {
            showModal: false,
            reasonOldFlame: false,
            reasonStudyDisease: false,
            reasonFindCure: false,
            selectedReason: {},
        }
    },
    methods: {
        closeBackground() {
            this.resetReasons();
            this.$emit('backgroundClose', this.selectedReason);
        },
        closeBackgroundApply() {
            if (JSON.stringify(this.selectedReason) === "{}") {
                this.showModalFn('No reason selected. Please select a reason!')
            } else {
                this.$emit('backgroundCloseApply', this.selectedReason)                
            }
        },
        chooseReason(reason, reasonNameDisplay, effect) {
            this.resetReasons();
            this[reason] = !this[reason];
            this.selectedReason.name = reason;
            this.selectedReason.reasonNameDisplay = reasonNameDisplay;
            this.selectedReason.effect = effect;
            
        },
        resetReasons() {
            this.reasonOldFlame = false;
            this.reasonStudyDisease = false;
            this.reasonFindCure = false;
        },
        showModalFn(msg) {
        this.infoMsg = msg;
        this.showModal = true;
        setTimeout ( () => {
        this.showModal = false;
        }, 1000)
    },
    }
}
</script>

<style scoped>
    .background-select {
    height: 100vh;
    width: 100%;
    display: grid;
    position: absolute;
    justify-content: center;
    align-content: center;
    justify-items: center;
    background: #000408;
    color: #BCE0D6;
    padding: 1rem 0;
    }

    .background-select p {
        font-size: 1.4rem;
        line-height: 1.8rem;
        padding: .5rem;
    }

    .reason-select-wrap {
        display: grid;
        grid-gap: 1rem;
        margin: .5rem;
        overflow-y: auto;
    }

    .reason {
        padding: .5rem;
        border: #BCE0D6 1px solid;
        transition: all .3s ease-in-out;
    }

    .reason p{
        font-size: 1.1rem;
        line-height: 1.4rem;
    }

    .reason h5 {
        font-size: 1.4rem;
    }

    .selected-reason {
        border: #AC7C0A 1px solid;
        
    }
</style>
