<template>
    <div class="inventory">
        <h1>Inventory</h1>
            <!-- show player how the stats change when he switches items -->
            <stat-display></stat-display>
        
        <transition name="component-opacity" mode="out-in">
        <div class="equipped-items-section" v-if="itemsDisplay" key="equippedItems">
            <!-- this section shows what the player has already equipped, can be switched with section that displays all the items the player has -->
            <div class="items-left items-slot">
                <div class="helmet inventory-slot" @click="swapItem('helmet')">helmet</div>
                <div class="weapon inventory-slot">
                    <img :src="require('../assets/icons/'+this.$store.state.playerCharacter.playerEquippedItems.helmet.imgUrl)" alt="" class="inventory-icon">
                </div>
                <div class="armour inventory-slot">
                    <img :src="require('../assets/icons/'+this.$store.state.playerCharacter.playerEquippedItems.armor.imgUrl)" alt="" class="inventory-icon">
                    
                </div>
            </div>
            <div class="portrait-center">
                <div class="portrait-img">
                    <img src='../assets/portraits/portrait.jpg' alt="">

                </div>
            </div>
            <div class="items-right items-slot">
                <div class="ring-1 inventory-slot">ring1</div>
                <div class="ring-2 inventory-slot">ring2</div>
                <div class="future-item inventory-slot">1 more slot</div>

            </div>
            <div class="utility-belt items-slot">
                <div class="belt-1 inventory-slot">quick item 1</div>
                <div class="belt-2 inventory-slot">quick item 2</div>
                <div class="belt-3 inventory-slot">quick item 3</div>
                
                
            </div>
        </div>

        <!-- switched with the previous section, this section displays all of the items in the inventory -->
        <div class="inventory-content-items" v-if="backpackDisplay" key="inventoryItems">

            <!-- helmets -->
            <div class="items-display-wrap helmets-display">
                <p>Helmets:</p>
                <div class="display-section helmets-section-line">
                    <div class="inventory-slot">helmet v for</div>
                </div>
            </div>

            <!-- armors -->
            <div class="items-display-wrap armours-display">
                <p>Armours:</p>
                <div class="display-section armours-section-line">
                    <div class="inventory-slot">armours v for</div>
                    
                </div>
            </div>

            <!-- weapons  -->
            <div class="items-display-wrap weapons-display">
                <p>Weapons:</p>
                <div class="display-section weapons-section-line">
                    <div class="inventory-slot">weapons v for</div>
                </div>
            </div>

            <!-- rings -->
            <div class="items-display-wrap rings-display">
                <p>Weapons:</p>
                <div class="display-section rings-section-line">
                    <div class="inventory-slot">rings v for</div>
                </div>
            </div>

            <!-- talismans -->
            <div class="items-display-wrap talismans-display">
                <p>Talismans:</p>
                <div class="display-section talismans-section-line">
                    <div class="inventory-slot">talismans v for</div>
                </div>
            </div>

            <!-- consumables -->
            <div class="items-display-wrap consumables-display">
                <p>Consumables:</p>
                <div class="display-section consumables-section-line">
                    <div class="inventory-slot">consumables v for</div>
                </div>
            </div>

        </div>
        </transition>
       
        <div class="inventory-buttons buttons-wrap-list btns-padding-bot">
            <div class="btn" @click="switchInventoryDisplay" >{{currentInventoryDisplay}}</div>
            <div class="btn" @click="closeInventory">CLOSE</div>
        </div>

        <!-- list of items modal that appears when clicked on a slot -->
        <div class="available-items-list" v-if="availableItemsOpen">
            <h2>Swap <span class="highlight"> {{itemToSwap}}</span></h2>
            <p>Current item:</p>
            <div class="current-item-slot-display inventory-slot">current item</div>
            <div class="equipped-items-stats items-stats">
                <p>Current item's name</p>
                <p>stat1</p>
                <p>stat</p>
                <p>stat</p>
                <p>stat</p>
            </div>
            
            <p>Available items:</p>
            <div class="items-to-equip-container">
                <div class="item-to-equip inventory-slot">v-for here</div>
            </div>
             <div class="equipped-items-stats items-stats">
                <p>selected items name</p>
                <p>stat1</p>
                <p>stat</p>
                <p>stat</p>
                <p>stat</p>
            </div>
            <div class="buttons-wrap-list ">
                <div class="btn">APPLY</div>
                <div class="btn" @click="availableItemsOpen=false">CANCEL</div>
                
            </div>
            
        </div>

    </div>
</template>

<script>
import statDisplay from './statDisplayComponent.vue';
export default {
    components: {
        statDisplay
    },
    data() {
        return {
            currentInventoryDisplay: "Backpack",
            backpackDisplay: false,
            itemsDisplay: true,
            availableItemsOpen: false,
            itemToSwap: ''
        }
    },
    methods: {
        switchInventoryDisplay() {
            this.currentInventoryDisplay=="Equipped" ? this.currentInventoryDisplay = "Backpack" : this.currentInventoryDisplay="Equipped";
            this.backpackDisplay = !this.backpackDisplay;
            this.itemsDisplay = !this.itemsDisplay;
        },
        closeInventory() {
            this.$emit('closeInventory')
        },
        swapItem(item) {
            this.itemToSwap = item;
            this.availableItemsOpen = true;
        }
    }
}
</script>

<style scoped>
.inventory {
    height: 100vh;
    width: 100%;
    display: grid;
    position: absolute;
    align-content: center;
    background: #000408;
    color: #BCE0D6;
    
}
.inventory h1 {
    justify-self: center;
    align-self: center;
    margin-bottom: 1rem;
}

.equipped-items-section {
    padding-top: 1rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: .5rem;
    justify-items: center;
    align-items: center;
    margin: 0 .5rem 0 .5rem;
    transition: .3s all ease-in-out;
}

.inventory-slot {
  border: #BCE0D6 1px solid;
  height: 64px;
  width: 64px;
}

.utility-belt {
    grid-column: span 3;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

.items-slot {
    display: grid;
    grid-gap: .5rem;
}

/* backpack items section */

.inventory-content-items {
    padding: 1rem;
    overflow-y: auto;
    transition: .3s all ease-in-out;
    
}

.display-section {
    padding: .5rem;
    border: 1px solid #BCE0D6;
    display: flex;
    flex-direction: row;
    grid-gap: .5rem;
}

.items-display-wrap p {
    margin-bottom: .3rem;
    font-size: 1.3rem;
}

/* stats section*/

.statistics {
    background: #D8CFBA;
    -webkit-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
  -moz-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
  box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
    color: #000408;
    padding: 1rem;
    overflow-x: auto;
    max-height: 160px;
    margin-bottom: .5rem;
    display: grid;
    grid-gap: .5rem;
    grid-template-columns: repeat(2, 1fr);
}

/*switch item - available items bar*/

.items-stats {
    display: grid;
    grid-template-columns: repeat(3, 1fr);    
    padding: 1rem;
    align-content: center;
    align-items: center;
    
}

/* modal that appears when switching items */

.available-items-list {
    position: absolute;
    display: grid;
    grid-gap: 1rem;
    top: 50%;
    transform: translate(0%, -50%);
    background: #000408;
    width: 100%;
    justify-items: center;
    border-top: #BCE0D6 1px solid;
    border-bottom: #BCE0D6 1px solid;
    padding: .5rem 0 .5rem 0;
    
}

.items-to-equip-container {
    overflow-x: auto;
    max-height: 128px;
}

.inventory-buttons {
    padding-top: 1rem;
    padding-bottom: 1rem;
    
}
</style>
