<template>
    <div class="combat-component">

        <div class="combat-display-wrapper">

            <!-- info display section -->
            <div class="information-display-wrapper scroll">
                <!-- v-for for combat effects in an array somewhere -->
                <p  v-for="(information, index) in combatInfoArray" :key="index">{{information}}</p>
            </div>

            
            <div class="effects-display-section" >
                <transition name="show-component-opacity">
                
                <div class="effects-list" v-if="combatEffectsPresent">
                    <!-- v-for for effects here below -->
                    <div class="effect icon" @click="displayEffectInfo('effectStatsPassed')">
                        <img src="../assets/icons/werewolf.svg" alt="">
                    
                    </div>

                </div>
                </transition>
            </div>
            

            <div class="enemy-cards-section cards-section">
                <!-- v-for for enemy units -->
                <div class="unit-card"  v-for="(unit, index) in enemyDummyUnits" :key=index @click="targetOrInfoAction(unit)">
                    <div class="unit-image portrait" :id="unit.positionName">
                        <img :src="require('../assets/portraits/' + unit.imgUrl)" alt="">
                    </div>

                    <div class="card-name">{{unit.name}}</div>
                    <div class="card-healthbar-container" >
                        <div class="card-healthbar" :style="{width: unit.hp +'px'}"></div>
                    </div>
                    
                </div>
            </div>

            <div class="player-cards-section cards-section">
                <!-- v-for for player units -->
                <div class="unit-card " @click="targetOrInfoAction(unit)" v-for="(unit, index) in allyDummyUnits" :key=index>
                    <div class="unit-image portrait " :id="unit.positionName">
                        <img :src="require('../assets/portraits/' + unit.imgUrl)" alt="">
                    </div>

                    <div class="card-name">{{unit.name}}</div>
                    <div class="card-healthbar-container">
                        <div class="card-healthbar" :style="{width: unit.hp +'px'}"></div>
                    </div>
                    <!-- <div class="player-action-points">Action points: {{unit.actionPoints}}</div> -->
                    
                </div>
            </div>

            <div class="buttons-wrap">
                <div class="btn" @click="closeEncounter">Close</div>
                <div class="btn" @click="helpDebug">show effects</div>
                <div class="btn" @click="displayPlayerActions">Action</div>
            </div>
            <!-- modals for displaying various info, like effects details and card details -->
            <!-- modal for player actions -->

            <transition name="show-component-bot">
            <div class="player-actions-wrap" v-if="showPlayerActions">
                <div class="player-actions-info">
                    <div>Available Action Points: {{playerAvailableApperTurn}}</div>
                    <div>Attack selected: {{currentplayerAttackTargeter}}</div>
                </div>

                <div class="player-weapons-info">
                    <div class="weapons-action-display">
                        <div>Weapon I: weapon selected</div>
                        <div class="icon">weapon icon?</div>
                    </div>
                    <div class="weapons-swit">
                        <div class="btn">Switch weapon</div>
                    </div>
                    
                    
                </div>
                
                <ul class="player-actions-list">
                    <li @click="resetPlayerAttackMode" class="btn">Deselect the attack</li>
                    <li @click="playerSelectMeleeAtk" class="btn">ATTACK</li>
                    <li @click="endPlayerTurn" class="btn">END TURN</li>
                    <li @click="showPlayerActions=false" class="btn">close</li>
                    
                </ul>
            </div>
            </transition>

            <!-- battle effect details modal -->
            <transition name="component-opacity">
            <div class="effect-detail-container small-absolute-container" v-if="showEffectDetail">
                <div class="effect-icon-container">
                    <img class="icon-medium" src="../assets/icons/werewolf.svg" alt="">
                    <p>Full Moon Night</p>
                    <p>Every creature touched by the curse of lycantropy dreads this baleful night.</p>
                    <p class="highlight">+1 to initiative and action points for every lycantrope</p>
                </div>
                <div class="buttons-wrap">
                    <div class="btn" @click="showEffectDetail=false">Close</div>
                </div>
            </div>
            </transition>

            <!-- card detail info modal -->
            <transition name="component-opacity">
            <div class="card-info-detail small-absolute-container" v-if="cardInfoDisplay">
                <div class="card-info-wrap">
                    <img class="portrait-medium" :src="require('../assets/portraits/' + chosenPlayerTarget.imgUrl)" alt="">
                    <p>name</p>
                    <p>description</p>
                    <p>weapons if applicable (if the enemy is a hunter or sth, or just in general); hp if awareness ability chosen</p>
                    <p>active buffs and debuffs, and maybe other effects depending on the player character's awareness and knowledge</p>                    
                </div>
                <div class="buttons-wrap" >
                    <div class="btn" @click="cardInfoDisplay=false">Close</div>
                </div>
            </div>
            </transition>

            <transition name="show-info-modal">
            <info-modal v-if="showModal" :msg="infoMsg"></info-modal>
            </transition>

        </div>

    </div>

</template>

<script>
import infoModal from '../components/infoModal.vue'
import {  setTimeout } from 'timers';

export default {
    components: {
        infoModal
    },
    data() {
        return {
            combatEffectsPresent: false,
            showEffectDetail: false,
            cardInfoDisplay: false,
            showModal: false,
            infoMsg: '',
            effectToDisplay: {},
            //calculate health based on enemy and player hp; should be a separate value for each enemy and allied unit
            healthLength: 100,
            enemyDummyUnits: [{hp: 100, dmg: 10, initiative: 1, actionPoints: 4, owner: 'enemy', side: 'enemy', name: 'enemy dummy', imgUrl:'portrait.jpg', positionName:'e1'},{hp: 100, dmg: 10, initiative: 3, actionPoints: 4, owner: 'enemy', side: 'enemy', name: 'enemy dummy', imgUrl:'portrait.jpg', positionName:'e2'}],
            allyDummyUnits: [{hp: 100, dmg: 9, actionPoints: 4, initiative: 2, owner: 'player', side: 'ally', name: 'player dummy', imgUrl:'portrait.jpg', positionName:'p3'},],

            combatInfoArray: ['combat starts'],

            //player actions variables
            currentplayerAttackTargeter: 'none',
            showPlayerActions: false,
            playerAttackTargeter: false,
            chosenPlayerTarget: {},
            currentPlayerActionPoints: null,
            playerCharacter: {},
            playerAvailableApperTurn: null,
            

            //battle variables
            unitsToMove: [],
            unitsMoved: [],
            playerWon: false,
            enemyWon: false,
            playerTurn: false,
            currentActiveUnit: {},
            
        }
    },
    methods: {
        helpDebug() {
            this.combatEffectsPresent=!this.combatEffectsPresent
        },
        updateScroll() {
            let scroll = this.$el.querySelector('.scroll');
            scroll.scrollTop = scroll.scrollHeight;
        },
        displayPlayerActions(){
            this.showPlayerActions = true;
        },
        closeEncounter() {
            this.$emit('closeCombat')
        },
        updateInfoArray(message) {
            this.combatInfoArray.push(message);
        },
        displayModalInfo(msg) {
            let self = this;
            this.showModal = true;
                this.infoMsg = msg
                setTimeout(function () {
                    self.showModal = false;
                
                }, 1000);
        },
        displayEffectInfo(name) {
            if (!this.playerAttackTargeter) {
                this.effectToDisplay = {effectName: name}
                this.showEffectDetail = true;
            }
            
        },
        displayCardInfo() {
            this.cardInfoDisplay = true;
        },
        checkForDeadUnits() {
            //check player health here too
            //also remove the dead unit from unitsToMove and unitsMoved arrays
            
            this.enemyDummyUnits.forEach((unit, index) => {
                if (unit.hp <= 0) {
                    this.enemyDummyUnits.splice(index, 1);
                    let toMovePosition = this.unitsToMove.indexOf(unit);
                    this.unitsToMove.splice(toMovePosition, 1)
                    let movedPosition = this.unitsMoved.indexOf(unit);
                    this.unitsMoved.splice(movedPosition, 1)
                }
            })

            this.allyDummyUnits.forEach((unit, index) => {
                if (unit.hp <= 0) {
                    this.allyDummyUnits.splice(index, 1);
                    let toMovePosition = this.unitsToMove.indexOf(unit);
                    this.unitsToMove.splice(toMovePosition, 1)
                    let movedPosition = this.unitsMoved.indexOf(unit);
                    this.unitsMoved.splice(movedPosition, 1)
                }
            })
            

            if (this.enemyDummyUnits.length <= 0) {
                this.updateInfoArray('win lol')
                this.playerWon = true;
            }

            if (this.allyDummyUnits.length <= 0) {
                this.updateInfoArray('lose lol')
                this.enemyWon = true;
            }

        },
        async startBattle() {
            //check if there are any enemies or allies left
            //if no allies, or the player is dead display death screen
            //if no enemies, display victory message and end combat; check for dead units does that
            this.checkForDeadUnits();
            if (this.playerWon || this.enemyWon) {
                console.log('fight has ended')
                return;
            }
            
            //check if there is something in this.unitsToMove q
            //if there isn't, pus everything from unitsMoved to unitsToMove q
            if (this.unitsToMove.length == 0) {
                this.unitsToMove.push(...this.unitsMoved);
                this.updateInfoArray('begins next turn')                
            }

            //take the 1st unit to move
            this.currentActiveUnit = this.unitsToMove[0];
           //add the active border to the unit that is about to move
            this.$el.querySelector('#'+this.currentActiveUnit.positionName).classList.add('card-active');

            //update all the ap values according to their modifiers for the player and enemies
            //formula will look like character.actionPoints + character.actionPointsModifiers
            this.playerAvailableApperTurn = this.playerCharacter.actionPoints;
            
            
            
            //if it's the player turn then give him control
            if (this.currentActiveUnit.owner == 'player') {
                this.playerTurn = true;
                this.updateInfoArray('player move')
                
                this.playerMoveFunction();
            }

            //if it's the enemy's turn then await a function (enemyMove() {return new Promise (resolve => {let self = this}))
            if (this.currentActiveUnit.owner == 'enemy' && !this.playerTurn) {
                this.updateInfoArray('enemy move')
                
                await this.enemyMoveFunction();
            }

            //resolve player turn by attack and use abilities actions

            //if player pressess end turn end this function

            //remove the unit that just moved
            this.unitsMoved.push(this.unitsToMove[0]);
            this.unitsToMove.splice(0, 1);

            //if there are any effects to display, either visually or to display, use a setTimeout
            //and display everything that needs a few sec

            
            if (!this.playerWon && !this.playerTurn) {
                // this.startBattle();
                this.updateInfoArray('end of the combat cycle, start nxt one')
                
            }
            this.updateScroll();
                        
        },
        enemyMoveFunction() {
            return new Promise (resolve => {
                let self = this;
                this.updateInfoArray(this.currentActiveUnit.name + '\'s turn')
                
                setTimeout(function() {
                    //ai here
                    //first pick a unit that will be attacked
                    let targetForAI = self.allyDummyUnits[0];
                    self.updateInfoArray(self.currentActiveUnit.name + ' attacks ' + self.allyDummyUnits[0].name )
                    
                    //now deal dmg to that unit
                    self.dealDmg(self.playerCharacter, self.currentActiveUnit)
                    self.$el.querySelector('#'+self.currentActiveUnit.positionName).classList.remove('card-active');
                
                    resolve("enemyMoveFunction");
                }, 1000)
                
            })
        },
        playerMoveFunction() {
            let self = this;
            this.playerTurn = true;
            
        },
        targetOrInfoAction(target) {
            if (!this.playerAttackTargeter) {
                this.cardInfoDisplay = true;
                this.chosenPlayerTarget = target;
                
            } else if (this.playerTurn) {
                this.chosenPlayerTarget = target;
                //this should take another parameter describing if this is an attack or a spell, and then apply
                //an appropriate effect, like dealDmg if its an auto attack, something else if a spell, etc
                //the parameter can be a global variable or passed to the targetOrInfoAction(target, action) as the 2nd parameter
                
                //check for action points here?
                if ((this.playerAvailableApperTurn-2)< 0) {
                    this.displayModalInfo('Not enough action points!')
                    
                } else {
                    this.dealDmg(this.chosenPlayerTarget, this.playerCharacter);                    
                }
            }
        },
        // deal dmg function
        dealDmg(target, attacker) {
            //below are the checks that forbid from attacking self and allies with auto attacks
            if (attacker === this.playerCharacter && target === this.playerCharacter) {
                this.displayModalInfo('invalid target');
                return;
                
            } else if (target.side ==='ally' && attacker === this.playerCharacter) {
                this.displayModalInfo('invalid target');
                return;
            }

            //establish this for setInterval use, because this points to something else in an inner fn like that
            let self = this;

            
            let targetIdentityUnits = target.side + 'DummyUnits'
            
            //deduct action points for the enemies and the player
            this.playerAvailableApperTurn -=2;
            //this function should not only apply dmg but also all the various status effects
            //that go with the player action
            
            //maybe it should be an available targets array, which contains all the units, in case
            //an ally could be targeted (by a healing spell for instance);
            //if the player tries to heal an enemy or attack an ally an error could be displayed
            let currentTargetPosition = this[targetIdentityUnits].indexOf(target);
            //make code for player instance and not just taking him from 1st place in ally array
            this[targetIdentityUnits][currentTargetPosition].hp -= attacker.dmg
            this.updateInfoArray(target.name + ' was hit for ' + attacker.dmg + ' dmg')

            // apply animation
            this.$el.querySelector('#'+target.positionName).classList.add('hit-anim');
            
            setTimeout(function() {
            self.$el.querySelector('#'+target.positionName).classList.remove('hit-anim');
                
            }, 500)
            
            this.updateScroll();
            
            this.checkForDeadUnits();
        },
        //player actions functions
        playerSelectMeleeAtk() {
            let self = this;
            //code below runs when an action was selected
            if (this.playerTurn) {
                this.playerAttackTargeter = true;
                this.currentplayerAttackTargeter = "Melee attack (AP cost: 2)";
                this.displayModalInfo('select target for melee attack');
            }
           
        },
        endPlayerTurn() {
            this.playerAttackTargeter = false;
            this.currentplayerAttackTargeter = "none";
            this.$el.querySelector('#'+this.currentActiveUnit.positionName).classList.remove('card-active');
            this.playerTurn = false;
            this.startBattle();
            
        },
        resetPlayerAttackMode() {
            this.playerAttackTargeter = false;
            this.currentplayerAttackTargeter = "none"
            this.updateInfoArray('Attack cancelled')
            
        },
    },
    computed: {
        
    },
    created() {
        
        //push every unit into to move queue and then sort it by initiative
        this.unitsToMove.push(...this.enemyDummyUnits);
        this.unitsToMove.push(...this.allyDummyUnits);
        this.allyDummyUnits.forEach( unit => {
            if (unit.owner == "player") {
                this.playerCharacter = unit;
                return;
            }
        })
        
        this.unitsToMove.sort((a, b) => {
            return b.initiative - a.initiative
        })
        
        //apply units to their respective cards by their owners

        //fire start battle function
        
    },
    mounted() {
        this.startBattle();
    }
}
</script>

<style scoped>
    .combat-component {
        height: 100vh;
        width: 100%;
        display: grid;
        position: absolute;
        background: #000408;
        padding: .5rem;

    }

    /* info display */
    .information-display-wrapper {
        background: #D8CFBA;
        color: #000;
        padding: .5rem 1rem .5rem 1rem;
        -webkit-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
        -moz-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
        box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
        height: 102px;
        overflow-y: auto;
        margin-bottom: 1rem;
    }

    /* combat effects style */

    .effects-display-section {
        height: 50px;
        
    }
    .effects-list {
        transition: all .3s ease-in-out;
        display: flex;
        flex-direction: row;
        grid-gap: .5rem;
    }


    /* card image style */

    .unit-card {
        display: grid;
        justify-items: center;

    }

    .card-active {
        border: 3px solid red;
    }

    .card-name {
        justify-self: center;
    }

    .cards-section {
        display: flex;
        grid-gap: 1rem;
        margin: 1rem;
        flex-direction: row;
        justify-content: center;
    }

    .card-healthbar-container {
        height: 5px;
        border: 1px solid #BCE0D6;
        width: 100%;
        max-width: 100%;
    }

    .card-healthbar {
        transition: all .3s ease-in-out;        
        height: 4px;
        background-color: red;
    }
    /* player actions */

    .player-actions-wrap {
        background: #000408;
        border-top: #BCE0D6 1px solid;
        position: absolute;
        bottom: 0;
        left:0;
        width: 100%;
        transition: all .3s ease-in-out;
        display: grid;
        grid-template-rows: 1fr 1fr 1fr;
        padding: .3rem;
        grid-gap: .3rem;
    }

    .player-actions-list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: .5rem;
    }

    .player-actions-list li {
        border: #BCE0D6 1px solid;
        padding: .1rem;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .player-weapons-info {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: .4rem;
        align-items: center;
    }

    .weapons-action-display {
        display: flex;
    }

    /* alpha hit animation */

.hit-anim {
    position: relative;
    
}

.hit-anim::before {
    content: '';
    position: absolute;
    background: url('../assets/icons/slash-transparent.svg') no-repeat;
    animation: hitAnimation .3s forwards;
    height: 100%;
    width: 100%;
    max-width: auto;
    top: 0;
    right: 0;
}
/* ALPHA keyframes hit animation*/
@keyframes hitAnimation {
    0% {
        opacity: 0;
        transform: rotate(0);
        margin: 0;
        
    } 50% {
        opacity: 1;
        transform: rotate(45deg);
        margin-left: 15px;
        margin-top: 12px;
        
        
    } 100% {
        opacity: 0;
        transform: rotate(60deg);
        margin: 0;
    }
}

@keyframes poisonAnimation {
    0% {
        opacity: 0;
    } 50% {
        opacity: 1;
    } 100% {
        opacity: 0;
    }
}

@keyframes deathAnimation {
    from {
        opacity: 0;
    } to {
        opacity: 1;
    } 
    
}
    
</style>
