<template>
    <div class="encyclopedia">
        <div class="main-view" v-if="!entryViewOpen">
            <h1>Encyclopedia</h1>
            <ul class="encyclopedia-list">
                <li class="btn">CLASSESS</li>
                <li class="btn">ABILITIES</li>
                <li class="btn">STATISTICS</li>
                <li class="btn">some other</li>
            </ul>
        </div>
        <div class="entry-view" v-if="entryViewOpen">
            {{entry}}:
            {{passedEntry}}
            
            <div class="btn" @click="entryViewOpen=false" >CLOSE</div>
            
        </div>
        <div class="btn" @click="closeEncyclopedia">EXIT</div>
        
        
    </div>
</template>

<script>
export default {
    props: ['category', 'entry'],
    data() {
        return {
            abilities: [{Arcane: 'Arcane explanation here lol'}],
            mainViewOpen: true,
            entryViewOpen: false,
        }
    },
    methods: {
        closeEncyclopedia() {
            this.$emit('closeEncyclopedia');
        }
    },
    computed: {
        passedEntry() {
            let category = this.category;
            let entry = this.entry;
            let num = null;
            this[category].forEach( (e, index) => {if (Object.keys(e)[0] == entry) {num = index}})
            return this[category][num][entry];
        },
    },
    mounted() {
        if (!this.entry && !this.category) {
            this.entryViewOpen=false;
        } else {
            this.entryViewOpen = true;
        }
        
    }
}
</script>

<style scoped>
.encyclopedia {
    position: absolute;
    background: #000408;
    color: #BCE0D6;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    display: grid;
    justify-content: center;
    align-content: center;
    z-index: 5;
}

</style>
