<template>
    <div class="stat-display-container">

        <div class="character-stats-container">
            <div class="char-name-section">
                <p>Name: {{this.$store.state.playerCharacter.characterName}}</p>
                <p>character class: {{this.$store.state.playerCharacter.characterClass}}</p>
                <p >Character's Raison D'etre: {{this.$store.state.playerCharacter.charBackground.reasonNameDisplay}} ({{this.$store.state.playerCharacter.charBackground.effect}})</p>
                
            </div>
            <div class="char-stats-section">
                <ul class="character-stats">
                    <li class="bold-on-paper">Statistics:</li>
                    <li v-for="(stat, index) in playerCharacter.statistics" :key=index>{{stat.name}}: {{stat.value}}</li>
                    <li>Hit points: {{charHPcomp}}</li>
                    <li>Action points: {{charAPcomp}}</li>
                    <li>Bleakness: {{this.$store.state.playerCharacter.bleakness}}</li>

                </ul>
            </div>
            <div class="char-abilities-section">
                    <p class="bold-on-paper">Abilities:</p>                
                <ul class="character-abilities" v-for="(ability, index) in this.$store.state.playerCharacter.abilities" :key=index>
                    <li v-if="ability.status">{{ability.name}}</li>
                </ul>
            </div>
                
            
        </div>
    </div>
</template>

<script>
export default {
    computed: {
    playerCharacter() {
      return this.$store.state.playerCharacter;
    },
    charHPcomp() {
      return this.$store.getters.characterHp;
    },
    charAPcomp() {
      return this.$store.getters.actionPoints;
    }
  }
}
</script>

<style scoped>
.stat-display-container {
    padding: 1rem;
    display: grid;
    color: #000;
    background: #D8CFBA;
    -webkit-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
    -moz-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
    box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
    overflow-y: auto;
    /* margin: 0 .5rem 0 .5rem; */
    }
</style>
