import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const gameStore = new Vuex.Store({
    state: {
        gameState: {
            isNewGame: false,
            characterCreated: false
        },
        playerCharacter: {
            characterName: '',
            statistics: [{name: 'Vitality', value: 1}, {name: 'Strength', value: 1}, {name:'Dexterity', value: 1}, {name: 'Arcane', value: 1}, {name:'Luck', value: 1}],
            characterClass: '',
            abilitiesPreset: null,
            characterName: '',
            pointsToAllocate: 5,
            abilityPoints: 1,
            charBackground: {reasonNameDisplay: '', effect: ''},
            bleakness: 0,
            
            //inventory
            playerEquippedItems: {helmet: {name: 'helmet', imgUrl: 'blunderbuss.svg', slot: 'helmet'}, armor: {name: 'armor', imgUrl: 'armor.svg'}},
            playerInventoryHelmets: [],
            generalAbilityModifiers: {HPmodifier: 0, initiativeModifier: 0, APmodifier: 0 },
            hunterAbilityModifiers: {snareTraps: false, trapsLevel: 0},
            engineerAbilityModifiers: {craftArmour: false, armourLevel: 0},
            

            //abilities
            abilitySets: [ {abilitySetChar: 'engineer', abilities: [{name:'ability engineer dummy1', desc: 'dummy engineer desc', effectDesc: 'effect desc'}, {name: 'ability engineer dummy2', desc: 'dummy engineer desc 2', effectDesc: 'effect desc'}]}, {abilitySetChar: 'hunter', abilities: [{name:'Snare trap', desc: 'Basic type of a trap. Simple to set yet highly effective.', effectDesc: 'Activates after 1 turn. Lowers enemy initiative by 1 for 3 turns.', status: 0}, {name: 'ability hunter dummy2', desc: 'dummy hunter desc 2', effectDesc: 'effect desc'}]}, {abilitySetChar: 'scholar', abilities: [{name:'ability scholar dummy1', desc: 'dummy scholar desc', effectDesc: 'effect desc'}, {name: 'ability scholar dummy2', desc: 'dummy scholar desc 2', effectDesc: 'effect desc'}]}],
            abilities: [{innerName: 'manofaction', name: 'Man of action', desc: 'You could never sit in one place for too long.', effectDesc: 'Increases action points by 1', target: 'APmodifier', targetValue: 1, status: 0, abilityType: 'modifier', locked: false}],
       
        },
        gameProgressionRoute: {},
        
    },
    getters: {
        characterHp: state => { return state.playerCharacter.statistics[0].value*10},
        actionPoints: state => {return (state.playerCharacter.statistics[2].value * 2) + state.playerCharacter.generalAbilityModifiers.APmodifier }
    },
    mutations: {
        decreaseStat: (state, stat) => {
            switch (stat) {
                case "Vitality":
                if(state.playerCharacter.statistics[0].value <=1) {
                    break;
                } else {
                    state.playerCharacter.statistics[0].value --;
                    state.playerCharacter.pointsToAllocate ++;
                }                    
                break;

                case "Strength":
                 if(state.playerCharacter.statistics[1].value <=1) {
                    break;
                } else {
                    state.playerCharacter.statistics[1].value --;
                    state.playerCharacter.pointsToAllocate ++;
                }
                break;

                case "Dexterity":
                 if(state.playerCharacter.statistics[2].value <=1) {
                    break;
                } else {
                    state.playerCharacter.statistics[2].value --;
                    state.playerCharacter.pointsToAllocate ++;
                }
                break;

                case "Arcane":
                 if(state.playerCharacter.statistics[3].value <=1) {
                    break;
                } else {
                    state.playerCharacter.statistics[3].value --;
                    state.playerCharacter.pointsToAllocate ++;
                }
                break;

                // case "Spirit":
                //  if(this.statistics[4].value <=1) {
                //     break;
                // } else {
                //     this.statistics[4].value --;
                //     this.pointsToAllocate ++;
                // }
                // break;

                case "Luck":
                 if(state.playerCharacter.statistics[4].value <=1) {
                    break;
                } else {
                    state.playerCharacter.statistics[4].value --;
                    state.playerCharacter.pointsToAllocate ++;
                }
                break;
            }
        },
        increaseStat: (state, stat) => {
            if (state.playerCharacter.pointsToAllocate > 0) {
                state.playerCharacter.pointsToAllocate --;
                switch (stat) {
                    case "Vitality":
                    state.playerCharacter.statistics[0].value ++;
                    break;

                    case "Strength":
                    state.playerCharacter.statistics[1].value ++;
                    break;

                    case "Dexterity":
                    state.playerCharacter.statistics[2].value ++;
                    break;

                    case "Arcane":
                    state.playerCharacter.statistics[3].value ++;
                    break;

                    // case "Spirit":
                    // this.statistics[4].value ++;
                    // break;

                    case "Luck":
                    state.playerCharacter.statistics[4].value ++;
                    break;
                }
            } else {
                return;
            }
            
        },
        increaseBleakness: (state, value) => {
            state.playerCharacter.bleakness += value;
        },
        selectAbility: (state, {ability, index}) => {
            if (ability.locked === true) {
                return;
            } 

            if ( state.playerCharacter.abilities[index].status === 0) {
                state.playerCharacter.abilities[index].status = 1;
                state.playerCharacter.abilityPoints--;
                if (ability.abilityType === 'modifier') {
                    state.playerCharacter.generalAbilityModifiers[ability.target] += ability.targetValue
                    }
            } else if (state.playerCharacter.abilities[index].status === 1) {
                    state.playerCharacter.abilities[index].status = 0;
                    state.playerCharacter.abilityPoints++
                
                if (ability.abilityType === 'modifier') {
                    state.playerCharacter.generalAbilityModifiers[ability.target] -= ability.targetValue
                    }
            }
            
        },
        confirmAbility: (state, {ability, index}) => {
            state.playerCharacter.abilities[index].locked = true;
            
        }
        
    },
    actions: {
  
    }

})