<template>
    <div class="travel-component">
        <h1 class="locations">{{currentLocation}}</h1>

        <transition name="map" mode="out-in">
        <div class="map-container town-map" v-if="townMap" key="town-map1">
            <div class="hunters-hall" @click="huntersHallOpen=true">
                <img src="../assets/images/huntersHall.svg" alt="">
                <p>Hunters' Hall</p>
            </div>
            <div class="enginners-guild">
                <img src="../assets/images/engineersGuild.svg" alt="">
                <p>Engineers' Guild</p>
            </div>
            <div class="cynthias-clinic">
                <img src="../assets/images/cynthiasClinic.svg" alt="">
                <p>The Clinic</p>
            </div>
            <div class="scholars-guild">
                <img src="../assets/images/scholarsGuild.svg" alt="">
                <p>Sholars' Guild</p>
            </div>
            <div class="den-gambling">
                <img src="../assets/images/gambling.svg" alt="">
                <p>Den of Outlaws</p>
            </div>
            <div class="leave-town" @click="switchMaps">
                <img src="../assets/images/leaveTown.svg" alt="">
                <p>Leave Town</p>
            </div>
        </div>

        <div class="map-container wild-map" v-if="wildernessMap" key="wild-map2">
            <div class="back-town" @click="switchMaps">
                <img src="../assets/images/backToTown.svg" alt="">
                <p>Return to Town</p>
            </div>
            <div class="tomb">
                <img src="../assets/images/tomb.svg" alt="">
                <p>Forgotten Tomb</p>
            </div>
            <div class="dark-forest" >
                <img src="../assets/images/darkenwood.svg" alt="">
                <p>Darken Wood</p>
            </div>
        </div>
        </transition>

        <div class="buttons-container buttons-wrap-list">
            <div class="btn" @click="openChar">CHARACTER</div>
            <div class="btn" @click="combatEncounter=true">combat</div>

        </div>

        <!-- places -->
        <hunters-hall v-if="huntersHallOpen" @leaveHuntersHall='huntersHallOpen=false'></hunters-hall>
        <!-- character sheet modal -->

        <character-sheet v-if="characterOpen" @closeChar='openChar'></character-sheet>
        <!-- story board modal -->
        <story-board v-if="storyBoardOpen" @closeStoryBoard='storyBoardOpen=false'></story-board>

        <!-- combat component -->
        <combat-component v-if="combatEncounter" @closeCombat="combatEncounter=false"></combat-component>
    </div>
</template>

<script>
import characterSheet from './characterSheet.vue';
import storyBoard from './storyBoard.vue'
import huntersHall from '../components/places/huntersHall.vue'
import combatComponent from './combatComponent.vue'


export default {
    components: {
        characterSheet,
        storyBoard,
        huntersHall,
        combatComponent
    },
    data() {
        return {
            characterOpen: false,
            townMap: true,
            wildernessMap: false,
            storyBoardOpen: false,

            // locations 
            huntersHallOpen: false,

            //game state events, combat, dialogue, story event (with a storyboard), etc
            combatEncounter: false
            
        }
    },
    methods: {
        openChar() {
            this.characterOpen = !this.characterOpen;
        },
        switchMaps() {
            this.townMap = !this.townMap;
            this.wildernessMap = !this.wildernessMap;
        }
    },
    computed: {
        currentLocation() {
            if (this.townMap == true) {
                return "Town Map"
            } else {
                return "Hunting Grounds"
            }
        }
    }
}
</script>

<style scoped>
.travel-component {
    height: 100vh;
    width: 100%;
    display: grid;
    grid-template-rows: 1fr 5fr 1fr;
    background: #000408;
    color: #BCE0D6;
    justify-content: center;
    align-items: center;
    }

.map-container {
    background: url('../assets/images/map.svg') no-repeat;
    background-size: cover;
    background-position: center center;
    margin: .5rem;
    display: grid;
    grid-template-rows: repeat(4, 1fr);
    grid-template-columns: repeat(5, 1fr);
    transition: .3s all ease-in-out;
    padding: .5rem;
    
}
.locations {
    text-align: center;
}

.map-container p{
    color: #000408;
    text-align: center;
}

/* town map grid */

.leave-town {
    grid-column: 3;
    grid-row: 4;
}

.cynthias-clinic {
    grid-column: 5;
    grid-row: 3;
}

.den-gambling {
    grid-column: 1;
    grid-row: 3;
}

.scholars-guild {
    grid-column: 3;
    grid-row: 2;
}

.hunters-hall {
    grid-column: 4;
    grid-row: 1;
}

.engineers-guild {
    grid-column: 2;
    grid-row: 1;
}

/* wild map */

.back-town {
    grid-column: 3;
    grid-row: 1;
}

.tomb {
    grid-column: 1;
    grid-row: 3;
}

.dark-forest {
    grid-column: 2;
    grid-row: 4;
}

/* map transition */

.map-enter {
    opacity: 0;
}
.map-enter-to {
    opacity: 1;
}
.map-leave-to {
    opacity: 0;
    
}
</style>
