import Vue from 'vue'
import Router from 'vue-router'
import mainMenu from './views/mainMenu.vue'
import characterCreation from './views/characterCreation.vue'
import gameSection from './components/travelScreen.vue'
import redirectNotFound from './components/redirect/redirectNotFound.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'mainMenu',
      component: mainMenu
    },
    {
      path: '/characterCreation',
      name: 'characterCreation',
      
      component: () => import(/* webpackChunkName: "about" */ './views/characterCreation.vue')
    },
    {
      path: '/gameSection',
      name: 'gameSection',
      
      component: () => import(/* webpackChunkName: "about" */ './components/travelScreen.vue')
    },
    {
      path: '/:pathMatch(.*)*',
      component: redirectNotFound
    }
  ]
})
