<template>
    <div class="abilities-distribution-component">
        <h1>Choose abilities</h1>
        <div class="ability-points-display">Points to allocate: <span class="highlight">{{availavleAbilityPoints}}</span></div>
        <div class="abilities-display">
            <div class="class-abilities-wrap" v-if="classAbilitiesPresent">
                <div class="ability general-ability" v-for="(ability, index) in abilities" :key='index' @click="selectAbility(ability, index)" :class="{'selected-ability': ability.status  }" :id="ability.innerName">
                    <p class="ability-name">{{ability.name}}</p>
                    <p class="ability-description">{{ability.desc}}</p>
                    <p class="effect-description highlight">{{ability.effectDesc}}</p>
                </div>
                
                <div class="class-ability ability" v-for="(ability, index) in classAbilities" :key='"2nd"+index' :id="ability.innerName">
                    <p class="ability-name">{{ability.name}}</p>
                    <p class="ability-description">{{ability.desc}}</p>
                    <p class="effect-description highlight">{{ability.effectDesc}}</p>
                    
                </div>
            </div>
            <div class="no-class-selected" v-if="!classAbilitiesPresent">
                Select a character class to choose abilities
            </div>
            
           
        </div>

        <!-- <div class="selected-abilities">
           <p class="character-effects-title">Character effects:</p>
           <div class="effects-wrapper" v-for="(effect, index) in effects" :key="index">
               <p class="highlight">{{effect}}</p>
           </div>
        </div> -->

        <div class="buttons-wrap-list">
            <div class="btn-close btn" @click="closeAccept">ACCEPT</div>
            <div class="btn-close btn" @click="closeCancel">CANCEL</div>
        </div>
        
        
    </div>
</template>

<script>
export default {
    data() {
        return {
            classAbilities: this.$store.state.playerCharacter.abilitySets[this.$store.state.playerCharacter.abilitiesPreset].abilities,
            
            currentlySelectedAbilities: [],
            availavleAbilityPoints: this.$store.state.playerCharacter.abilityPoints
        }
    },
    methods: {
        selectAbility(ability, index) {
            if (ability.locked) {
                return;
            } else if ( this.availavleAbilityPoints <0) {
                return;
            }
            let newAbility = {'ability': ability, 'index': index}
            let self = this;

           if (!JSON.stringify(this.currentlySelectedAbilities).includes(JSON.stringify(ability)) ) {
                this.currentlySelectedAbilities.push({'ability': ability, 'index': index});
                this.$el.querySelector("#" +ability.innerName).classList.add('selected-ability');
                this.availavleAbilityPoints--;
            } else {
                this.availavleAbilityPoints++;
                this.currentlySelectedAbilities.forEach( (e, index) => {
                    if (JSON.stringify(e) === JSON.stringify(newAbility)) {
                        self.currentlySelectedAbilities.splice(index, 1);
                this.$el.querySelector("#" +ability.innerName).classList.remove('selected-ability');
                        
                    }
                })
            }

        },
        closeCancel() {
            //does not commit changes made only in the component to the store

            
            this.$emit('closeAbilities')
        },
        closeAccept(){
            //commits changes made in the component to the store
            this.confirmAbilities()
            this.$emit('closeAbilities')
            
        },
        confirmAbilities() {
            
            this.currentlySelectedAbilities.forEach(e => {
                this.$store.commit('selectAbility', e);
                this.$store.commit('confirmAbility', e);
            })
            this.$store.state.playerCharacter.abilityPoints = this.availavleAbilityPoints;
        }
    },
    computed: {
        classAbilitiesPresent() {
            if (this.$store.state.playerCharacter.abilitiesPreset == null) {
                return false
            } else { return true}
        },
        abilities() {
            return this.$store.state.playerCharacter.abilities;
        } 
    }
}
</script>

<style scoped>
.abilities-distribution-component{
    background: #000408;
    color: #BCE0D6;
    height: 100vh;
    width: 100%;
    display: grid;
    position: absolute;
    justify-content: center;
    align-content: center;
    }
    .abilities-distribution-component h1 {
        text-align: center;
        margin-bottom: 1rem;
    }

    .ability-points-display {
        font-size: 1.4rem;
        line-height: 1.8rem;
        margin-bottom: 1rem;
    }

    .abilities-display {
        margin-bottom: 1rem;
    }

    .abilities-wrap {
        overflow-y: scroll;
        display: grid;
        
    }

    .ability {
        border: 1px solid #BCE0D6;
        padding: 1rem;
        margin: .5rem 1rem;
        width: 300px;
        max-width: 300px;
        transition: .4s all ease-in-out;
        cursor: pointer;
    }

    .ability-name {
        font-size: 1.4rem;
        line-height: 1.8rem;
    }

    .ability-description {
        line-height: 1.4rem;
    }

    .effect-description {
        line-height: 1.4rem;
    }

    .selected-ability {
        border: 1px solid #AC7C0A;
        
    }

    .selected-abilities {
        margin-bottom: 1rem;
    }

    @media(min-width: 400px ) {
        .ability:hover {
        border: 1px solid #AC7C0A;
        }
    }
</style>
