<template>
    <div class="stats-distribution">
        <h1>Distribute statistic points</h1>
        <div class="stat-description">
            <p>Remaining points: <span class="highlight">{{pointsToAllocate}}</span></p>
            
        </div>
        <div class="statistics-list">
            <div class="stat" v-for="(stat, index) in statistics" :key="index">
                <div class="stat-name int-stat "><span class="highlight" @click="openEncyclopedia('abilities', stat.name)"> {{stat.name}}</span>: {{stat.value}}</div>
                <div class="int-increase stat-manip" @click="increaseStat(stat, index)"><span>+</span></div>
                <div class="int-decrease stat-manip" @click="decreaseStat(stat, index)"><span>-</span></div>
                
            </div>
        </div>
        <div class="buttons-wrap-list">
            <div class="apply-button btn" @click="acceptStats">ACCEPT</div>
            <div class="back-button btn" @click="cancelStats">CANCEL</div>
        </div>
        <!-- encyclopedia -->
        <encyclopedia v-if="encyclopediaOpen" @closeEncyclopedia="encyclopediaOpen=false" :category="encyclopediaCategory" :entry="encyClopediaEntry"></encyclopedia>
    </div>
</template>

<script>
import encyclopedia from './encyclopedia.vue'
export default {
    components: {
        encyclopedia
    },
    data() {
        return{
            // statistics: ,
            
            encyclopediaOpen: false,
            encyclopediaCategory: null,
            encyClopediaEntry: null,
            statMinValues: this.$store.state.playerCharacter.statistics

            //player character object
        }
    },
    methods: {
        openEncyclopedia(category, entry) {
            this.encyClopediaEntry = entry;
            this.encyclopediaCategory = category;
            this.encyclopediaOpen=true;
        },
        acceptStats() {
            
            this.$emit('closeStats');
        },
        cancelStats() {
            this.$store.state.playerCharacter.statistics = [{name: 'Vitality', value: 1}, {name: 'Strength', value: 1}, {name:'Dexterity', value: 1}, {name: 'Arcane', value: 1}, {name:'Luck', value: 1}];
            this.$store.state.playerCharacter.pointsToAllocate = 5;
            this.$emit('closeStats');
        },
        increaseStat(stat, index) {

            this.$store.commit('increaseStat', stat.name);
            
        },
        decreaseStat(stat, index) {
            if (this.statMinValues[index].value >= (stat.value -1) && this.$store.state.gameState.characterCreated) {
                return;
            }
            this.$store.commit('decreaseStat', stat.name)  
            
        }

    },
    computed: {
        pointsToAllocate() {
            return this.$store.state.playerCharacter.pointsToAllocate;
        },
        statistics() {
            return this.$store.state.playerCharacter.statistics;
        }
    }
}
</script>

<style scoped>
.stats-distribution {
    background: #000408;
    color: #BCE0D6;
    height: 100vh;
    width: 100%;
    display: grid;
    position: absolute;
    justify-content: center;
    align-content: center;
    }

.stats-distribution h1 {
    margin-bottom: 1rem;
}

.stats-distribution p{
        font-size: 1.5rem;
        line-height: 1.8rem;
        margin-bottom: .5rem;
    }

.statistics-list {
    display: grid;
    overflow-y: scroll;
}

.stat {
    display: grid;
    grid-template-columns: 3fr 1fr 1fr;
    grid-gap: 1rem;
    margin-bottom: 1rem;
}

.stat-manip {
    border: 1px solid #BCE0D6;
    padding: .3rem;
    text-align: center;
}

.stat-name {
    font-size: 1.8rem;
    line-height: 2.2rem;
    padding: .5rem;
}
.stat-manip span {
    font-size: 2.4rem;
    
}


</style>
